* {
    box-sizing: border-box;
}

body {
    font-size: rem(18px);
    color: black !important;
    margin: 0;
    width: 100vw;
    height: 100vh;
    padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0 0 0.6em;
    line-height: 1;
}

.MuiTextField-root{
    & .MuiOutlinedInput-root {
    &.Mui-focused{
        fieldset {
            border-color: $clr1;
        }
    } 
}
}	
